@import url(https://fonts.googleapis.com/css2?family=Roboto+Mono:wght@200&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

:root {
  /* CSS HSL */
--cerulean-blue: hsla(226, 46%, 46%, 1);
--cerulean-frost: hsla(208, 43%, 59%, 1);
--middle-blue-green: hsla(162, 45%, 72%, 1);
--cultured: hsla(160, 10%, 94%, 1);
--maximum-yellow-red: hsla(40, 89%, 61%, 1);
}

* {
  box-sizing: border-box;
  padding: 0;
  margin: 0;
}

body {
  font-family: 'Roboto Mono', monospace;
  background-color: hsla(226, 46%, 46%, 1);
  background-color: var(--cerulean-blue);
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: center;
          justify-content: center;
  -webkit-align-items: center;
          align-items: center;
}

.pad-bank {
  padding: 10px;
  display: grid;
  grid-gap: 2px;
  grid-template-rows: 100px 100px 100px;
  grid-template-columns: 100px 100px 100px;
  color: white;
  background-color: hsla(208, 43%, 59%, 1);
  background-color: var(--cerulean-frost);
  border-radius: 0 0 5px 5px;
}

.display {
  margin-top: 20px;
  padding-top: 20px;
  background-color:  hsla(208, 43%, 59%, 1);
  background-color:  var(--cerulean-frost);
  text-align: center;
  color: black;
}

.drum-pad {
  padding-top: 40px;
  text-align: center;
  border-radius: 2px;
  background-color: black;
  outline: none;
  border: none;
}

.drum-pad:hover {
  padding-top: 43px;
  background-color: rgb(146, 145, 145);
  color: white;
}
